// @flow weak

import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import MenuIcon from 'material-ui-icons/Menu';
import image1 from '../images/clapclapp2.png'
const styles = theme => ({
    root: {
        marginTop: 0,
        width: '100%',
    },
    flex: {
        flex: 1,
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
});

function ClapClappBar(props) {
    const { classes } = props;
    return (
        <div className={classes.root}>
            <AppBar position="fixed" color="default" >
                <Toolbar >
                    <IconButton className={classes.menuButton} color="default" aria-label="Menu">
                        <MenuIcon />
                    </IconButton>
                    <img src={image1} alt="ClapClapp"/>


                </Toolbar>
            </AppBar>

        </div>
    );
}

ClapClappBar.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ClapClappBar);


