// @flow weak

import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Card, { CardActions, CardContent, CardMedia } from 'material-ui/Card';
import Typography from 'material-ui/Typography';
import IconButton from "material-ui/IconButton/IconButton";
import FavoriteIcon from 'material-ui-icons/Favorite';
import ShareIcon from 'material-ui-icons/Share';
import Grid from "material-ui/Grid/Grid";
import Badge from "material-ui/Badge/Badge";
import Dialog from "material-ui/Dialog/Dialog";
import PersonFull from "./PersonFull";
const styles = {
    card: {
        maxWidth: 300,
        minWidth: 300,
        maxHeight:270,
        minHeight: 270,
    },
    media: {
        height: 150,
        width: 300,
    },
    actions: {
        justify: 'center',
    }
};
class SimpleDialog extends React.Component {
    handleRequestClose = () => {
        this.props.onRequestClose(this.props.selectedValue);
    };

    handleListItemClick = value => {
        this.props.onRequestClose(value);
    };

    render() {
        const { classes, onRequestClose, selectedValue,image, ...other } = this.props;
        console.log()
        return (
            <Dialog onRequestClose={this.handleRequestClose} {...other}>
               {/*<img src={image}/>*/}
                <PersonFull image={image}/>

            </Dialog>
        );
    }
}

SimpleDialog.propTypes = {
    classes: PropTypes.object.isRequired,
    onRequestClose: PropTypes.func,
    selectedValue: PropTypes.string,
};

const SimpleDialogWrapped = withStyles(styles)(SimpleDialog);

class Person extends React.Component {
    state = {
        open: false,
        // selectedValue: emails[1],
    };

    handleClickOpen = () => {
        this.setState({
            open: true,
        });
    };

    handleRequestClose = value => {
        this.setState({ selectedValue: value, open: false });
    };

    render () {
        const {classes,name} = this.props;
        console.log(classes)
        const min = 1;
        const max = 100;
        const rand = Math.round(min + Math.random() * (max - min));
        const imageBla = "https://source.unsplash.com/random/500x250";
        return (
            <div>
                <Card className={classes.card}>
                    <CardMedia
                        className={classes.media}
                        image={imageBla}
                        title="Contemplative Reptile"
                        onClick={this.handleClickOpen}
                    />
                    <CardContent>
                        <Typography type="headline" component="h2">
                            {name}
                        </Typography>
                    </CardContent>
                    <CardActions disableActionSpacing>
                        <Grid container className={classes.actions}
                              alignItems={'center'}
                              direction={'row'}
                              justify={'center'}
                        >
                            <IconButton aria-label="Add to favorites">

                                <Badge
                                    badgeContent={rand}
                                >
                                    <FavoriteIcon/>
                                </Badge>
                            </IconButton>
                            <IconButton aria-label="Share">
                                <ShareIcon/>
                            </IconButton>
                        </Grid>
                    </CardActions>
                </Card>
                <SimpleDialogWrapped
                    selectedValue={this.state.selectedValue}
                    open={this.state.open}
                    onRequestClose={this.handleRequestClose}
                    image={imageBla}
                />
            </div>
        );
    }
}

Person.propTypes = {
    classes: PropTypes.object.isRequired,
};


export default withStyles(styles)(Person);


